<template>
  <div class="box">
    <h3 class="header">
      <span>科室管理</span>
      <el-button type="primary" @click="add" >新增科室</el-button>
    </h3>
    <el-table :data="departmentList">
        <el-table-column prop="id" label="ID">
        </el-table-column>
        <el-table-column prop="dValue" label="一级科室名称">
        </el-table-column>
        <el-table-column prop="address" label="所属二级科室">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="goodsManageClick(scope.row)">新增二级科室</el-button>
            <el-button type="text" size="small" @click="serveClick(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    <add-book :title="title" :dialogVisible.sync="dialogVisible" :label="label" v-on:control="control" :input="input"></add-book>
    <div class="demonstration-box" v-show="false">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
  </div>
</template>

<script>
const queryString = require('querystring')
import page from 'data/page'
import addBook from '../../components/addBook'
import { departmentList } from 'data/listBody';
export default {
  name: 'department',
  mixins: [page],
  data() {
    return {
      title: '新增科室',
      label: '科室名字',
      input: '',
      dialogVisible: false,
      departmentList,
      currentPage: 1
    }
  },
  components: {
    addBook
  },
  methods: {
    add() {
      this.title = '新增科室';
      this.dialogVisible = true;
    },
    control(val) {
      console.log(val)
      if (!val) {
        this.dialogVisible = false
      } else {
        // this.edit()
      }
        this.dialogVisible = false
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    handleClick(row) {
      console.log(row);dValue
      this.input = row.comName;
      this.title = '编辑科室';
      this.dialogVisible = true;
    },
    goodsManageClick() {
      this.title = '二级科室';
      this.dialogVisible = true;
    },
    serveClick(row) {
      this.$confirm('此操作将删除该行信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.axios.delete(`/dict/${row.id}`).then((res) => {
            debugger
            if (res.data.code == 200) {
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            } else {
              this.$message({
                type: 'info',
                message: '删除失败！'
              });
            }
          })
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }
  }
}
</script>

<style lang="less" scoped>
.box{
  padding-left: 20px;
}
.header{
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-between
}
</style>
